import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function payablesTurnoverTtm(tableName: string) {
  const title = "Payables Turnover (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["payablesTurnoverTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "How often payables are paid in a year.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about payables turnover or other debtors relevant information?`,
              value: info.row.original.financialData.companyRatios?.data?.payablesTurnoverTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyRatios?.data?.payablesTurnoverTtm}
        />
      )
    },
  })
}

export { payablesTurnoverTtm }
